.sign-upButton {
    display: block;
    width: 100%;
    height: 50px;
    max-width: 200px;
    line-height: 50px;
    position: relative;
    top: -318px;
    left: 430px;
    margin: 50px 0;
    animation: fadeInAnimation 1s 1.8s backwards;
    white-space: nowrap;
    color:#212020;
    font-family: Arial, Helvetica, sans-serif;
}

.sign-upButton div {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: 0 15px 0 15px;
  }
  
  .sign-upButton span.bg {
    width: 0%;
    height: 100%;
    position: absolute;
    left: -5%;
    top: 0;
    transform: skewX(-10deg);
    transition: all 0.3s linear;
  }
  
  .sign-upButton:hover span.bg {
    background: var(--color-tem);
    width: 110%;
    border-radius: 0 15px 0 15px;
  }
  
  .sign-upButton span.base {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0 15px 0 15px;
    border: 1px solid var(--color-tem);
  }
  
  .sign-upButton span.text {
    text-align: center;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
  }
  
  .sign-upButton:hover span.text {
    color: #2d2b2b;
  }